    thresholdForm.quotaName = scope.row.quotaName
    thresholdForm.enable = -1
    request({
      url: '/hmis/monitor/rule/queryAllQuotaWarnRules/v1.0',
      method: 'post',
      data: thresholdForm
    }).then((res: any) => {
