        if (rules.length > 0) {
          let rule = rules[0];
          thresholdForm.checkCount = rule.checkCount
          thresholdForm.notifyTimeoutSec = rule.notifyTimeoutSec
          thresholdForm.phoneNums = rule.phoneNums
